<template>
    <div class="bind-example">
        <nav-bar
            title="如何查看微信实名"
            class="header"
            :click-back-handler="backHandler"
            :finish-execute="true" />
        <div
            class="example-list"
            v-for="item in list"
            :key="item.src">
            <div class="example-title">{{ item.title }}</div>
            <img
                class="example-pic"
                :src="item.imgSrc" >
        </div>
    </div>
</template>

<script type="text/javascript">
import NavBar from '@/components/NavBar';
import { isTakeOverBackByWeb, setSwipeBackStatus } from '@/utils/jsApiV2/client';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';

export default {
    data() {
        return {
            list: [
                { title: '第一步：打开微信我的主页，点击支付', imgSrc: 'http://t1.market.xiaomi.com/download/Browser/0e6d1e454c7484746100bb411334e756c0ff98897' },
                { title: '第二步：点击更多', imgSrc: 'http://t1.market.xiaomi.com/download/Browser/0d6d1e454c7484747100b8411314e756c0ff08897' },
                { title: '第三步：点击实名认证', imgSrc: 'http://t1.market.xiaomi.com/download/Browser/0bf004ebb91626b905204655fe9ae68a92042a4ff' },
                { title: '姓名展示', imgSrc: 'http://t1.market.xiaomi.com/download/Browser/0f6d1e454c7584748100bd411324e75cc0ffa8897' },
            ],
        };
    },
    components: {
        NavBar,
    },
    methods: {
        backHandler() {
            this.$router.go(-1);
        },
    },
    created() {
        this.$root.hidePlaceholder();
    },
    mounted() {
        setSwipeBackStatus('false');
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backHandler);
    },
    destroyed() {
        setSwipeBackStatus('true');
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backHandler);
    },
};
</script>
